.CryptoWrapper {
    height: 1200px;
    width: 100%;
    margin-top: -5px;
    background: rgba(3, 14, 33, 1);
    background: -webkit-linear-gradient(top, rgba(3, 14, 33, 1) 0%, rgba(3, 14, 33, 1) 35%, rgba(0, 59, 85, 1) 96%, rgba(0, 59, 85, 1) 100%);
    background: -o-linear-gradient(top, rgba(3, 14, 33, 1) 0%, rgba(3, 14, 33, 1) 35%, rgba(0, 59, 85, 1) 96%, rgba(0, 59, 85, 1) 100%);
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(3, 14, 33, 1)), color-stop(35%, rgba(3, 14, 33, 1)), color-stop(96%, rgba(0, 59, 85, 1)), to(rgba(0, 59, 85, 1)));
    background: linear-gradient(to bottom, rgba(3, 14, 33, 1) 0%, rgba(3, 14, 33, 1) 35%, rgba(0, 59, 85, 1) 96%, rgba(0, 59, 85, 1) 100%);
}

.TitleText {
    margin: 0;
    color: white;
    font-weight: bold;
    font-size: 10rem;
    text-shadow: 0 2px 30px #21ce99;
    position: relative;
    width: 100%;
    top: 200px;
}

.CryptoWrapper h3 {
    color: white;
    font-size: 5rem;
}

.CryptoWrapper .CryptoTitle {
    list-style: none;
    margin: 0;
    padding: 0;
    position: relative;
    display: inline-block;
    top: 400px;
}

.CryptoTitle li {
    float: left;
    margin-left: 100px;
}

.CryptoTitle li:first-child {
    margin-left: 0;
}

.CryptoTitle a {
    display: block;
    cursor: pointer;
    text-decoration: none;
}

.CryptoTitle a.Bitcoin:hover {
    text-shadow: 0 2px 30px #21ce99;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.CryptoTitle a.Ethereum:hover {
    text-shadow: 0 2px 30px #f7f77e;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.CryptoTitle a.Litecoin:hover {
    text-shadow: 0 2px 30px #FF3399;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}




