.App {
    text-align: center;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

.HeaderTitle {
    background-color: transparent;
    font-weight: bold;
    font-size: 10rem;
    color: white;
    text-shadow: 0 2px 30px #21ce99;
    margin: 0;
    position: absolute;
    text-align: center;
    width: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 1;
}



